import React, { useContext } from 'react'
import { useNavigate, useSearchParams } from 'react-router-dom'
import { States } from '../Contest/Context'

function Head() {
    const { state,dispatch }=useContext(States)
    const navigation=useNavigate()
    // ==== forward function ===

    const navi=useNavigate()
    const [searchParam]=useSearchParams()
    const getId=searchParam.get('id')
    const getName=searchParam.get('name') 
    const getCerti=searchParam.get('certify') 
    const getGenres=searchParam.get('genres')
    const getLang=searchParam.get('lang')

    function MoveForward(){
        if(state.navigates=='Seats'){
            navigation(`/theaterlist/?id=${getId}&name=${getName}&certify=${getCerti}&genres=${getGenres}&lang=${getLang}`)
        }else if(state.navigates=='Payment'){
            dispatch({type:'hides',payload:true})
            navigation(`/seats/?id=${getId}&name=${getName}&certify=${getCerti}&genres=${getGenres}&lang=${getLang}`)
        }
    }

    // === function end ==

    function Removeseats(){
        let se=document.querySelectorAll('.selected')
        dispatch({type:'seatsOk',payload:0})
        if(state.ticketcount!==se.length){
            se.forEach((e)=>{
                e.classList.remove('selected')
            })
        }
        let Rupee=document.querySelector('.calculate')
            Rupee.innerHTML=''
        
    }

    // useEffect(Removeseats,[state.ticketcount])
  return (
    <div>
        <div className='container-fluid seat-top'>
            <div className='row pt-2 pb-3 align-items-center'>
                <div className='col-lg-6 col-8 d-flex align-items-center'>
                    <div>
                        <span onClick={MoveForward} class="text-light material-symbols-outlined cancelseats"> arrow_back_ios</span>
                    </div>
                    <div>
                        <div className='d-flex gap-2 align-items-center ms-2'>
                            <span className='text-light seat-title'>{state.m_detail.m_name}</span> 
                            <span className='text-light seat-cer'>{state.m_detail.m_cer}</span>
                        </div>
                        <div>
                            <div className='d-flex gap-2 align-items-center ms-2'>
                                <span className='seats-title'>{state.m_detail.t_name}:{state.m_detail.t_location}</span>
                                <span className='seats-title'>{state.m_detail.s_date},{state.m_detail.s_time}</span>
                            </div>
                        </div>
                    </div>
                </div>
                <div className='col-lg-6 col-4 d-flex align-items-center justify-content-end gap-1 gap-md-5'>
                   {state.hide ? <div className='text-light d-flex align-items-center border gap-2 edit-div' onClick={Removeseats} data-bs-toggle='modal'  data-bs-target="#staticBackdrop">
                        <p className='total-seats'><span>{state.ticketcount}</span> Tickets</p>
                        <span className='material-symbols-outlined seat-edit'>edit</span>
                    </div> : ' '}
                    <div className='text-light'>
                         <span className='material-symbols-outlined seats-cancel' onClick={MoveForward}>close</span>
                    </div>
                </div>
            </div>
        </div>
    </div>
  )
}

export default Head