import React, { useContext, useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import Slider from "react-slick";
import { States } from "../Contest/Context";
import Moivelist from "./Movie.json";
import "./Movies.css";

function Movies() {
  const [view,setView]=useState(1)
  const {state,dispatch}=useContext(States)
  const navigate=useNavigate()

  var settings = {
    dots: false,
    infinite: false,
    speed: 500,
    slidesToShow: 4,
    slidesToScroll: 4,
    initialSlide: 0,
    responsive: [
      {
        breakpoint: 1500,
        settings: {
          slidesToShow: 5,
          slidesToScroll: 5,
          infinite: false,
          dots: false,
        },
      },
      {
        breakpoint: 600,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 2,
          initialSlide: 2,
        },
      },
      {
        breakpoint: 480,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
        },
      },
    ],
  };

  function ShowNoMovie(){
   let allMovies=Moivelist.map((a)=>{
      if(a.name.toLowerCase().includes(state.isSearch)){
        return true
      }else{
        return false
      }
      
    })
    const isFalse = (e) => e==false;
    if(state.searchFlag){
      if(allMovies.every(isFalse)){
        setView(0)
      }else{
        setView(1)
      }
    }
    if(!state.searchFlag){
      setView(1)
    }
  }
  useEffect(ShowNoMovie,[state.isSearch,view,state.searchFlag])


  return (
    <div className='movies-slide'>
      <div className="container py-5">
        <div className="movielist-top d-flex justify-content-between px-2">
          <h5 className="text-start">Recommended Movies</h5>
          <a href="#" className="text-danger showAll d-flex justify-content-center align-items-center">
            See All
            <span class="all-arrow material-symbols-outlined">
              chevron_right
            </span>
          </a>
        </div>
        {view ? <Slider {...settings}>
          {Moivelist.map((a, b) => {
            if(state.isSearch==null || a.name.toLowerCase().includes(state.isSearch)){
               return (
                <div  className="cols position-relative" onClick={()=>{
                  navigate(`/moviedetail/?id=${a.id}&name=${a.name}&certify=${a.certi}&genres=${a.genres}&lang=${a.lang}`)
                }}>
                  <div className="img-card rounded-3">
                    <img src={a.url} alt="load" className="toShow" />
                  </div>
                  <div className="movie-detail d-flex flex-column">
                    <div>
                      <h6 className="text-start mt-2 fw-bold">{a.name}</h6>
                    </div>
                    <div>
                      <p className="text-start movie-genres">{a.genres}</p>
                    </div>
                  </div>
                </div> 
                
              )
            }
            
          })
          }
        </Slider> : <div className="py-3"> <p className="text-danger text-start">Movie Not Find!</p> </div>}
      </div>
    </div>
  );
}

export default Movies;
