import { typographyClasses } from '@mui/material';
import React from 'react'
import Slider from 'react-slick'
import './Banner.css'

function Banner() {
    const settings = {
        infinite: true,
        centerMode:true,
        speed: 500,
        centerPadding:"60px",
        slidesToShow: 1,
        slidesToScroll: 1,
        autoplay:true
      };
  return (
    <div>
        <div className='banner'>
            <div className='container-fluid border py-3 py-md-2 position-relative overflow-hidden'>
            <Slider {...settings}>
                <div className='ban-img'>
                    <img src='https://assets-in.bmscdn.com/promotions/cms/creatives/1682617779318_webbannernpa.jpg' alt='load' />
                </div> 
                <div className='ban-img'>
                    <img src='https://assets-in.bmscdn.com/promotions/cms/creatives/1687178459254_rednoolpresentsandrealivedesktop.jpg' alt='load' />
                </div>
                <div className='ban-img'>
                    <img src='https://assets-in.bmscdn.com/promotions/cms/creatives/1685964734910_summeractiviesdesktop.avif' alt='load' />
                </div>               
            </Slider>
            </div>
        </div>
    </div>
  )
}

export default Banner