import React, { useContext, useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { States } from "../Contest/Context";
import Head from "../Seats/Head";
import Foods from "./Food.json";
import "./Payment.css";


function Payment() {
  // ==== food toggle ===

  const[item,setItem]=useState('All')
  let allItems=document.querySelectorAll('.food-link')
  
  function UserSelectItem(e){
    for(let i=0;i<allItems.length;i++){
      if(allItems[i].classList.contains('actives')){
        allItems[i].classList.remove('actives')
      }
    }
    let selectItem=e.currentTarget.getAttribute('data-item')
    e.currentTarget.classList.add('actives')
    setItem(selectItem)
  }

  // === food toggle end ===

  const { state, dispatch } = useContext(States);
  const navi = useNavigate();

  const [convinence,setConvinence]=useState()

  function FindConvinence(){
    let t_seats=state.ticketcount
    let fee=t_seats*30
    let gst=(fee/100)*18
    setConvinence(gst+fee)
  }

  useEffect(FindConvinence,[])

  function Movetopay(){
    var options={
      key:'rzp_test_taONMU73Yb7OGn',
      key_secret:'txGlRHp71IBpcAfZHwVkWBJc',
      amount:(convinence + state.total)*100,
      currency:'INR',
      name:'Movie',
      description:'Online-Booking',
      handler:function(res){
        alert(res.razorpay_payment_id)
        dispatch({type:'submited',payload:!state.conformation})
    localStorage.setItem('list_id',JSON.stringify(state.list))
    localStorage.setItem('details',JSON.stringify(state.m_detail))
    navi("/Movie-Ticket")
      },
      prefill:{
        name:'alex', 
        email:'tsalexraja@gmail.com',
        contact:7871795673
      },
      notes:{
        address:"Razorpay Corporate office"
      },
      theme:'#33399cc'
    };
    var pay=new window.Razorpay(options)
    pay.open()
  }

  return (
    <div>
      <Head />

      {/* ======== food and seats details =========== */}

      <div className="conatiner-fluid">
        <div className="row w-100">
          <div className="order-2 order-lg-1 col-lg-8 border mt-2 mt-lg-0">
            <div className="row">
              <h4 className="heading-food">
                Grab a <span>bite!</span>
              </h4>
              <p className="food-booking">Prebook Your Meal and Save More!</p>
              <div className="foods col-lg-12 d-flex gap-3 justify-content-center align-items-center">
                <a href="#" className="food-link actives" data-item='All' onClick={UserSelectItem}>
                  All
                </a>
                <a href="#" className="food-link" data-item='Snacks' onClick={UserSelectItem}>
                  Snacks
                </a>
                <a href="#" className="food-link" data-item='Drinks' onClick={UserSelectItem}>
                  Drinks
                </a>
                <a href="#" className="food-link" data-item='Combo' onClick={UserSelectItem}>
                  Combo
                </a>
              </div>

              <div className="col-lg-12 d-flex flex-wrap mt-5">
                {Foods.map((e) => {
                   if(item =='All' || e.type==item){ 
                  return (
                    <div className=" col-12 col-md-6 p-2">
                      <div className="card py-1 d-flex justify-content-center flex-row align-items-center">
                        <div className="col-2">
                          <img src={e.url} alt="load" className="w-100" />
                        </div>
                        <div className="col-10 py-2 px-2 d-flex flex-wrap gap-5">
                          <div className="col-12 text-start">
                            <h4 className="mb-0 food-name">{e.type}</h4>
                            <span className="food-type">{e.item}</span>
                          </div>
                          <div className="col-12 d-flex justify-content-between">
                            <div> 
                            <span className="food-rate">Rs.{e.rate}</span>
                            </div>
                            <div>
                              <button className="food-add">Add</button>
                            </div>
                          </div>
                        </div>
                        
                      </div>
                    </div>
                  )
                  }
                })}
              </div>
            </div>
          </div>
          <div className="order-1 order-lg-2 col-10 col-md-7 mt-3 mt-lg-0 m-auto border border-lg-none col-lg-4 py-3">
            <p className="book-right-head">Booking Summary</p>
            <div className="col-12 list-of-seats d-flex justify-content-between">
              <div className="d-flex gap-1 align-items-center">
                <p className="right-numbers">
                  Seats-
                </p>
                <p>
                  {state.list.map((e) => {
                    return <span className="seats-numbers-final ms-1">{e}</span>;
                  })}
                </p>
                <p className="final-count">({state.ticketcount} tickets)</p>
              </div>
              <div className="">
                <span className="totals">Rs. {state.total.toFixed(2)}</span>
              </div>
            </div>

            <div className="col-12 list-of-seats d-flex justify-content-between convinence pb-2">
              <div className="d-flex gap-1 align-items-center">
                <span class="material-symbols-outlined show-more"> expand_more</span>
                <p className="fee">Convenience fees </p>
              </div>
              <div>
                <span className="totals">Rs. {convinence}</span>
              </div>
            </div>

            <div className="col-12 list-of-seats d-flex justify-content-between py-1">
              <div>
                <p className="sub">Sub Total </p>
              </div>
              <div>
                <span className="totals">Rs. {convinence + state.total}</span>
              </div>
            </div>

            <div className="px-3">
              <div className="pay-btn d-flex justify-content-between align-items-center" onClick={Movetopay}>
                <span>
                  Total: Rs. {convinence + state.total}
                </span>
                <span>Proceed</span>
              </div>
            </div>
              
          </div>
        </div>
      </div>

      
    </div>
  );
}

export default Payment;
