import React, { useContext, useEffect, useState } from 'react'
import './Nav.css'
import logo from '../../Image/logo.jpg'
import { States } from '../Contest/Context'
import { GoogleAuthProvider, signInWithPopup } from 'firebase/auth'
import { auth } from '../../Firebase'

 
function Nav() {
    const {state,dispatch}=useContext(States)
     const [values,setValues]=useState(false)

// sign in -----------------

const provider= new GoogleAuthProvider()


const signInWithGmail= ()=>{
    signInWithPopup(auth,provider)
    .then((res)=>{
        setValues(true)
    })
    .catch((error)=>{
      console.log(error);
    })
  }


    const [search,setSearch]=useState('')
    function SearchMovie(e){
        setSearch(e.target.value)        
    }

    useEffect(()=>{
        dispatch({type:'search',payload:search})
        if(search.length==0){
            dispatch({type:'boolean',payload:false})
        }else{
            dispatch({type:'boolean',payload:true})
        } 
    },[search])
  return (
    <div>
        <div id='navi'>
            <div className='container py-1'>
                <div className='row py-2 align-items-center'>
                    <div className='col-md-2 col-3 logo'>
                        <div className='col-md-8 col-12'>
                            <a href='#'><img src={logo} alt='loading' /></a>
                        </div>
                    </div>
                    <div class="col-md-5 col-3">
                        <div className='bg-light rounded-3 d-flex justify-content-center align-items-center user-search py-1'>
                            <span class="material-symbols-outlined fs-6 text-secondary">search</span>
                            <input type="text" class="border-0 ms-1 text-secondary" placeholder="Search here" name='search' value={search} onChange={SearchMovie} />  
                        </div>
                    </div>
                    <div className='col-6 offset-md-2 col-md-3 d-flex justify-content-center align-items-center nav-right'>
                        <a href='#' className='d-flex align-items-center text-decoration-none text-secondary'>Chennai<span class="material-symbols-outlined fs-5 text-light">arrow_drop_down</span></a>
                        <div className='position-relative'>
                            {values?<span className='material-symbols-outlined user-icons'>account_circle</span>:
                             <button onClick={signInWithGmail} type="button" class="btn btn-danger">Signin</button>}
                        </div>
                        
                        <span class="material-symbols-outlined text-light side-menu">menu</span>
                    </div>
                </div>
            </div>
        </div>
        <div id='links'>
            <div className='container'>
                <div className='row py-2'>
                    <div className='col-lg-5 col-md-7 d-none d-md-flex justify-content-between links-lists'>
                        <a href='#'>Movies</a>
                        <a href='#'>Stream</a>
                        <a href='#'>Events</a>
                        <a href='#'>Plays</a>
                        <a href='#'>Sports</a>
                        <a href='#'>Activities</a>
                        <a href='#'>Buzz</a>
                    </div>
                    <div className='offset-lg-3 col-md-5 col-lg-4 d-none d-md-flex justify-content-between links-list'>
                        <a href='#'>ListYourShow</a>
                        <a href='#'>Corporates</a>
                        <a href='#'>Offers</a>
                        <a href='#'>Gift Crds</a>
                    </div>

                    <div className='d-flex d-md-none'>
                        <div className='list-icons col-2'>
                            <i class="ri-movie-2-line text-light"></i>
                            <p className='text-light mb-0'>Movies</p>
                        </div>

                        <div className='list-icons col-2'>
                            <i class="ri-mic-line text-light"></i>
                            <p className='text-light mb-0'>Music</p>
                        </div>

                        <div className='list-icons col-2'>
                            <i class="ri-basketball-line text-light"></i>
                            <p className='text-light mb-0'>Sports</p>
                        </div>

                        <div className='list-icons col-2'>
                            <i class="bi bi-film text-light text-light"></i>
                            <p className='text-light mb-0'>Plays</p>
                        </div>

                        <div className='list-icons col-2'>
                            <i class="bi bi-film text-light"></i>
                            <p className='text-light mb-0'>Stream</p>
                        </div>

                        <div className='list-icons col-2'>
                            <i class="ri-stack-line text-light"></i>
                            <p className='text-light mb-0'>All</p>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
  )
}

export default Nav