import React from 'react'
import { useSearchParams } from 'react-router-dom'

function Panel() {
    const [Params]=useSearchParams()

    const M_name=Params.get('name')
    const M_cert=Params.get('certify')
    const M_gen=Params.get('genres')
    const M_lang=Params.get('lang')

    // console.log(M_gen.split('/'));
    const ListGenres=M_gen.split('/')

  return (
    <div className='panel' id={M_name}>
        <div className='container-fluid'>
            <div className='row'>
                <div className='col-12 py-2 py-md-0 pt-md-4 pb-md-3'>
                    <div className='d-flex'>
                        <h3 className='text-light'>{M_name} - {M_lang} </h3>
                    </div>
                    <div className='d-none d-md-flex gap-2'>
                        <div className='certificate text-light'>
                            {M_cert}
                        </div>
                        <div className='genres-list d-flex gap-2'>
                            {ListGenres.map((e)=>{
                                return(
                                    <div className='genre'>
                                        {e}
                                    </div>
                                )
                            })}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
  )
}

export default Panel