export const initialState={
    hide:false,
    main:true,
    ticketcount:0,
    m_detail:{},
    list:[],
    total:0,
    confirmation:false,
    isSearch:null,
    searchflag:false,
    navigates:null,
    searchFlag:false,
}

export const stateReducer = (state,action) => {
    switch (action.type){
        case 'boolean':
            return{
                ...state,
                searchFlag:action.payload,
            }
        case 'hides':
            return{
                ...state,
                hide:action.payload,
            }
        case "seatsOk":
            return{
                ...state,
                ticketcount:action.payload,
            }
        case "seatConfirm":
            return{
                ...state,
                m_detail:action.payload,
            }
        case 'confirm':
            return{
                ...state,
                main:action.payload,
            }
        case 'seatList':
            return{
                ...state,
                list:action.payload,
            }
        case 'amount':
            return{
                ...state,
                total:action.payload,
            }
        case 'submited':
            return{
                ...state,
                confirmation:action.payload
            }
        case 'search':
            return{
                ...state,
                isSearch:action.payload
            }
        case 'userNavi':
            return{
                ...state,
                navigates:action.payload
            }
        default:
            return state
    }
}