import React, { useReducer } from 'react'
import { BrowserRouter, Route, Routes} from 'react-router-dom'
import { States } from '../Contest/Context'
import { initialState, stateReducer } from '../Contest/Reducer'
import Home from '../HomeMain/Home'
import Moviedetail from '../MovieDetailsMain/Moviedetail'
import Payment from '../Payment/Payment'
import Razorpay from '../Razorpay/Razorpay'
import Scroll from '../Scroll/Scroll'
import Allseats from '../Seats/Allseats'
import Theaters from '../Theaters/Theaters'


function Routing() {
  const [state,dispatch]=useReducer(stateReducer,initialState)
  
  return (
    <States.Provider value={{state,dispatch}}>
   <BrowserRouter>
   <Scroll/>
        <Routes>
            <Route path='/Movie-Ticket' element={<Home />}></Route>
            <Route path='/moviedetail/' element={ <Moviedetail />}></Route>
            <Route path='/theaterlist/' element={ <Theaters />}></Route>
            <Route path='/seats/' element={<Allseats />}></Route>
            <Route path='/payment' element={<Payment />}></Route>
            <Route path='/razorpay' element={<Razorpay />}></Route>
        </Routes>
   </BrowserRouter>
   </States.Provider>
  )
}

export default Routing
